.countdown {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

.countdown div {
    margin: 2px;
    padding: 5px;
}

.countdown .value {
    background-color: var(--countdown-bg-color);
    width: 34px;
    text-align: center;
}

@media screen and (min-width: 550px) {
    .countdown {
        width: 50%;
        float: right;
        clear: both;
    }
}