.buyButton a {
    background-color: var(--buy-button-color);
    color: var(--main-text-color);
    height: 40px;
    width: 98%;
    border: none;
    border-radius: 5px;
    font-size: 150%;
    margin: 1%;
    -webkit-appearance: none;
    text-decoration: none;
    text-align: center;
    display:flex;
    align-items: center;
    justify-content: center;
}

.buyButton a:hover {
    cursor: pointer;
    box-shadow: inset 0px 0px 1px 1px var(--main-text-color);
    background-color: var(--buy-button-color-highlight);
}

@media screen and (min-width: 550px) {
    .buyButton {
        width: 50%;
        display: flex;
        justify-content: center;
    }
    .buyButton a {
        width: 50%;
    }
}