@import url(https://fonts.googleapis.com/css?family=Raleway);

:root {
    --main-bg-color: #2f3640;
    --main-text-color: #f5f6fa;
    --main-text-color-highlight: #dcdde1;
    --highlight-text-color: #7f8fa6;
    --buy-button-color: #44bd32;
    --buy-button-color-highlight: #4cd137;
    --burn-button-color: #c23616;
    --countdown-bg-color: #718093;
}
html, body {
    background-color: var(--main-bg-color);
    color: var(--main-text-color);
    font-family: 'Raleway', sans-serif;
    max-width: 1200px;
    margin: auto;
    padding: 10px;
}

@media screen and (min-width: 550px) {
    .buyCountdown {
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
    }
}
.carousel {
    width: 90vw;
    max-width: 1200px;
    display: grid;
    grid-template-areas: "ci ci pi"
                         "ci ci ni";
    grid-gap: 10px;
    margin-bottom: 10px;
}

.prevImage {
    display: none;
    grid-area: pi;
    width: 28vw;
    max-width: 390px;
    max-height: 295px;
}

.currentImage {
    grid-area: ci;
    width: 57vw;
    max-width: 795px;
    max-height: 600px;
}

.currentImage img {
    width: 57vw;
    max-width: 795px;
    max-height: 600px;
}

.nextImage {
    grid-area: ni;
    width: 28vw;
    max-width: 390px;
    max-height: 295px;
}
.title {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 50%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    color: var(--highlight-text-color);
}

@media screen and (min-width: 680px) {
    .title {
        font-size: 100%;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        -webkit-align-items: center;
                align-items: center;
    }
}
.buyButton a {
    background-color: var(--buy-button-color);
    color: var(--main-text-color);
    height: 40px;
    width: 98%;
    border: none;
    border-radius: 5px;
    font-size: 150%;
    margin: 1%;
    -webkit-appearance: none;
    text-decoration: none;
    text-align: center;
    display:-webkit-flex;
    display:flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.buyButton a:hover {
    cursor: pointer;
    box-shadow: inset 0px 0px 1px 1px var(--main-text-color);
    background-color: var(--buy-button-color-highlight);
}

@media screen and (min-width: 550px) {
    .buyButton {
        width: 50%;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
    }
    .buyButton a {
        width: 50%;
    }
}
.countdown {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 10px;
}

.countdown div {
    margin: 2px;
    padding: 5px;
}

.countdown .value {
    background-color: var(--countdown-bg-color);
    width: 34px;
    text-align: center;
}

@media screen and (min-width: 550px) {
    .countdown {
        width: 50%;
        float: right;
        clear: both;
    }
}
.description {
    margin-top: 20px;
}

.center {
    text-align: center;
}
.footer div a {
    text-decoration: none;
    color: var(--main-text-color);
}

.footer {
    font-size: 50%;
    text-align: right;
}
.header {
    display: grid;
    grid-template-areas: "socialLinks"
                         "logo       "
                         "briefDesc  ";
    grid-column-gap: 20px;
}

@media screen and (min-width: 680px) {
    .header {
        grid-template-areas: "logo socialLinks"
                             "briefDesc briefDesc";
        margin-bottom: 20px;
    }
}

.logo {
    margin: 10px auto;
}

.logoRaw {
    width: 100%;
}

@media screen and (min-width: 680px) {
    .logo {
        margin: 10px;
    }
    .logoRaw {
        width: 200px;
    }
}
.briefDesc {
    grid-area: briefDesc;
    padding: 20px;
}

@media screen and (min-width: 680px) {
    .briefDesc {
        padding: 0 20px;
    }
}
.socialLinks {
    grid-area: socialLinks;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    margin: 10px auto;
}

.svgLogo {
    width: 20px;
    height: 20px;
    margin-right: 5px;
    background-color: var(--main-text-color);
    padding: 5px;
    border-radius: 5px;
    box-shadow: 3px 3px #000;
}

.svgLogo:hover {
    background-color: var(--main-text-color-highlight);
    cursor: pointer;
}
