.socialLinks {
    grid-area: socialLinks;
    display: flex;
    justify-content: flex-end;
    margin: 10px auto;
}

.svgLogo {
    width: 20px;
    height: 20px;
    margin-right: 5px;
    background-color: var(--main-text-color);
    padding: 5px;
    border-radius: 5px;
    box-shadow: 3px 3px #000;
}

.svgLogo:hover {
    background-color: var(--main-text-color-highlight);
    cursor: pointer;
}