.title {
    display: flex;
    justify-content: center;
    font-size: 50%;
    justify-content: space-between;
    color: var(--highlight-text-color);
}

@media screen and (min-width: 680px) {
    .title {
        font-size: 100%;
        justify-content: space-around;
        align-items: center;
    }
}