.carousel {
    width: 90vw;
    max-width: 1200px;
    display: grid;
    grid-template-areas: "ci ci pi"
                         "ci ci ni";
    grid-gap: 10px;
    margin-bottom: 10px;
}

.prevImage {
    display: none;
    grid-area: pi;
    width: 28vw;
    max-width: 390px;
    max-height: 295px;
}

.currentImage {
    grid-area: ci;
    width: 57vw;
    max-width: 795px;
    max-height: 600px;
}

.currentImage img {
    width: 57vw;
    max-width: 795px;
    max-height: 600px;
}

.nextImage {
    grid-area: ni;
    width: 28vw;
    max-width: 390px;
    max-height: 295px;
}